.moduleMainContainerOverlay {
  position: fixed;
  width: 100vw;
  height: auto;
  top: 120px;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  z-index: 100;
  overflow: auto;
  background-color: rgba(33, 33, 33, 0.123);
}

.moduleMainContainer {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;

  background-color: white;
}

@media screen and (min-width: 768px) {
  .moduleMainContainerOverlay {
    top: 0;
    /* height: 599px; */
    background-color: rgba(33, 33, 33, 0.12);
  }

  .moduleMainContainer {
    top: 220px;
    width: 594px;
    height: 580px;
  }
}

@media screen and (min-width: 1280px) {
  .moduleMainContainer {
    top: 120px;
    width: 690px;
    height: 580px;
  }
}

.closeModalBtn {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  background-image: url('/src/icons/сlose.svg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  cursor: pointer;
  margin: 0;
  padding: 0;
  z-index: 120;
  border: none;
  outline: transparent;
  transition: all 0.25s linear;
}

@media screen and (max-width: 767px) {
  .closeModalBtn {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .closeModalBtn {
    display: block;
  }
}

.closeModalBtn:hover,
.closeModalBtn:focus {
  transform: scale(1.15);
  box-shadow: 0 0 25px lightgrey;
  border: 1px solid lightgrey;
}

@media screen and (max-width: 767px) {
  .closeModalBtn {
    display: none;
  }
}
